import { requestDownload } from 'utils';

const prefix = '/api/students';

export const exportAcademicStatistics = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  yearEduId,
  semesterId,
}) =>
  requestDownload({
    defaultFileName: 'File_AcademicStatistics.pdf',
    prefix,
    url: `/Student/ExportReportStudentAcademicStatistics`,
    method: 'POST',
    data: { trainTypeId, facultyId, courseId, studyFieldId, classId, yearEduId, semesterId },
    params: {}
  });



