import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col, Form, Card } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { YearSemesterInput } from 'components/common';
import { BtnClear, BtnExport } from 'components/common/button';
import { exportAcademicStatistics } from 'services/studentForAcademicStatistics';
import './style.scss';

@Form.create({ name: 'ReportAcademicStatisticsPage' })
class FormSearchStuForUnionsMem extends Component {
  state = {};

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.handleExport(values);
    });
  };

  handleExport = async values => {
    try {
      await exportAcademicStatistics(values);
    } catch (error) {
      console.log(error);
    }
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Kha',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn CN',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Nm hc',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Hc k',
          },
        ],
      },
    };
    return (
      <Card>
        <Form layout="vertical" className="form-inline-label report-academic-filter" onSubmit={this.handleSubmit}>
          <Row gutter={24}>
            <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
            <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} span={5} />
            <Col xs={24} sm={12} md={5} lg={5} xl={5} style={{ textAlign: 'center' }}>
              <div className="btn-group">
                <BtnExport className="btn-export-excel" title="Xut Excel" htmlType="submit" />
                <BtnClear onClick={this.handleReset} />
              </div>
            </Col>
          </Row>
        </Form>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <FormSearchStuForUnionsMem />
  </Page>
);



